<template>
    <div class="layout-px-spacing">  
        <ul class="navbar-nav flex-row mt-2">
            <li>
                <div class="page-header">
                    <nav class="breadcrumb-one" aria-label="breadcrumb">
                        <ol class="breadcrumb">
                            <li class="breadcrumb-item"><a href="javascript:;">Assets</a></li>
                            <li class="breadcrumb-item active" aria-current="page"><span>Fuel Cards </span></li>
                        </ol>
                    </nav>
                </div>
            </li>
        </ul>

        <h3 class="text-center" v-if="bincard">FUEL MANAGEMENT FILE FOR  {{ bincard.asset.name }} {{ bincard.asset.type_name }}</h3>

           <div class="d-flex me-4 mt-2 ">
                        <div class="ms-auto">
                             <button 
                                class="btn btn-primary refresh-btn rounded-circle p-2 me-2" 
                                @click="getAssetBincard()" 
                                title="Refresh page"
                                :class="{ 'spin-animation': loading}"
                                :disabled="loading"
                                style="width: 2.5rem; height: 2.5rem;"
                            >
                            <span v-if="loading" v-html="loadingSpinner()">

                            </span>
                                <svg 
                                    xmlns="http://www.w3.org/2000/svg" 
                                    width="16" 
                                    height="16" 
                                    fill="currentColor" 
                                    class="bi bi-arrow-clockwise" 
                                    viewBox="0 0 16 16"
                                >
                                    <path fill-rule="evenodd" d="M8 3a5 5 0 1 0 4.546 2.914.5.5 0 0 1 .908-.417A6 6 0 1 1 8 2v1z"/>
                                    <path d="M8 4.466V.534a.25.25 0 0 1 .41-.192l2.36 1.966c.12.1.12.284 0 .384L8.41 4.658A.25.25 0 0 1 8 4.466z"/>
                                </svg>
                            </button>

                            <button 
                            v-if="user_permissions.includes('can_register_asset_bincard')"
                            class="btn btn-primary me-2 " @click="register_bincard =! register_bincard">                           
                                <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" fill="currentColor" class="bi bi-plus-square" viewBox="0 0 16 16">
  <path d="M14 1a1 1 0 0 1 1 1v12a1 1 0 0 1-1 1H2a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1zM2 0a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2z"/>
  <path d="M8 4a.5.5 0 0 1 .5.5v3h3a.5.5 0 0 1 0 1h-3v3a.5.5 0 0 1-1 0v-3h-3a.5.5 0 0 1 0-1h3v-3A.5.5 0 0 1 8 4"/>
</svg>
</button>
                            <button class="btn btn-success "
                             @click="printBinCard(route.params.asset_id)" 
                             v-if="user_permissions.includes('can_print_asset_bincard')"
                            >                           
                              <svg xmlns="http://www.w3.org/2000/svg" width="26" height="26" fill="currentColor" class="bi bi-printer" viewBox="0 0 16 16">
  <path d="M2.5 8a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1"/>
  <path d="M5 1a2 2 0 0 0-2 2v2H2a2 2 0 0 0-2 2v3a2 2 0 0 0 2 2h1v1a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2v-1h1a2 2 0 0 0 2-2V7a2 2 0 0 0-2-2h-1V3a2 2 0 0 0-2-2zM4 3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1v2H4zm1 5a2 2 0 0 0-2 2v1H2a1 1 0 0 1-1-1V7a1 1 0 0 1 1-1h12a1 1 0 0 1 1 1v3a1 1 0 0 1-1 1h-1v-1a2 2 0 0 0-2-2zm7 2v3a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1v-3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1"/>
</svg>
</button>
 
                        </div>
                    </div>

       
         <!-- bincardTransactions registration form starts -->
 <form v-if="register_bincard" class="mt-3 p-3 border rounded bg-white shadow" @submit.prevent="registerAssetBincardTransaction()">
        <h4 class="text-center">REGISTER NEW RECORD</h4>
        <div class="row mb-2">
    
            <div class="col-md-2">
                <label class="form-label">Date</label>
                <input type="date" class="form-control" v-model="date" required>
            </div>
  <div class="col-md-2">
     <label class="form-label">Operation</label>
            <Multiselect 
        v-model="direction"
        :options="directions" 
        label="name"  
        placeholder="Select An Operation"
        :searchable="true"
        :close-on-select="true"
        required
      />    
  </div>


            <div class="col-md-3">
                <label class="form-label"> {{direction.name}} Amount  (LTs)</label>
                <input type="number" class="form-control" placeholder="Enter Amount" v-model="quantity" required step="any">
            </div>


                        
                        <div class="col-md-3">
                <label class="form-label">Initial Odometer Reading</label>
                <input type="number" class="form-control" placeholder="Enter Initial Reading" v-model="initial_reading" required>
            </div>


            <div class="col-md-2">
                <label class="form-label">Expected Ratio</label>
                <input type="number" class="form-control" placeholder="Enter Expected Ratio" v-model="ratio" required  step="any">
            </div>

            <div class="col-md-3">
                <label class="form-label">Work Target </label>
                <input type="number" class="form-control" placeholder="Enter Work Target" v-model="work_target" required>
            </div>

            <div class="col-md-3">
                <label class="form-label">Work Unit</label>
  <Multiselect 
        v-model="unit"
        :options="units" 
        label="short_name"  
        placeholder="Select Unit"
        :searchable="true"
        :close-on-select="true"
        required
      />    
            </div>

            <div class="col-md-6">
                <label class="form-label">Task</label>
                <input type="text" class="form-control" placeholder="Enter Task" v-model="task" step="any" required="">
            </div>

        </div>
          

        <div class="text-center gap-2 justify-content-end">
            <button type="button" class="btn btn-danger me-3" @click="register_bincard = false; resetForm();">Cancel</button>
            <button type="submit" class="btn btn-primary" :disabled="loading">
                <span v-if="loading" class="spinner-border spinner-border-sm me-1" role="status" aria-hidden="true"></span>
                <span v-else>Register</span>
            </button>
        </div>
    </form>
             <!-- bincardTransactions registration form ends-->



              <!-- loading spinner starts -->
        <div v-if="loading" v-html="tableShimmer()"></div>
        <!-- Loading spinner ends -->
        <div v-else class="row layout-top-spacing">
            <div class="col-xl-12 col-lg-12 col-sm-12 layout-spacing">
                <div class="panel p-0">
                    <div class="custom-table table3">
                        <v-client-table :data="bincardTransactions" :columns="columns" :options="table_option">
                            <template #actions="props">
                                <div class="actions text-center">

                                    <a href="javascript:;" class="edit me-3" @click="openEditModal(props.row)" data-bs-toggle="modal" :data-bs-target="'#bincardTransactions-edit-modal'">
                                        <svg
                                            xmlns="http://www.w3.org/2000/svg"
                                            width="24"
                                            height="24"
                                            viewBox="0 0 24 24"
                                            fill="none"
                                            stroke="currentColor"
                                            stroke-width="1.5"
                                            stroke-linecap="round"
                                            stroke-linejoin="round"
                                            class="text-success"
                                        >
                                            <path d="M11 4H4a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2h14a2 2 0 0 0 2-2v-7"></path>
                                            <path d="M18.5 2.5a2.121 2.121 0 0 1 3 3L12 15l-4 1 1-4 9.5-9.5z"></path>
                                        </svg>
                                    </a>
                                </div>                                 
                            </template>
                             <template #work_target="props">
                                {{ props.row.work_target }} {{ props.row.unit }}
                            </template>

                            <template #work_covered="props">
                                {{ props.row.work_covered ?? 0 }} {{ props.row.unit }}
                            </template>
                            
                        </v-client-table>
                    </div>
                </div>
            </div>
        </div>

             <!-- Edit bincardTransactions modal starts -->
<div class="modal fade mymodal" id="bincardTransactions-edit-modal" data-bs-backdrop="false" data-bs-keyboard="false" tabindex="-1" aria-labelledby="bincardTransactionsEditModalLabel" aria-hidden="true">
  <div class="modal-dialog modal-md">
    <div class="modal-content">
      <div class="modal-header">
        <h1 class="modal-title fs-5" id="bincardTransactionsEditModalLabel">Update Work Covered</h1>
        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"  id="close_editing"></button>
      </div>
      <form @submit.prevent="updateRecord()">
        <div class="modal-body">
          <div class="row mb-3">
            <div class="col-md-6">
              <label class="form-label">Final Reading</label>
              <input type="text" placeholder="Enter FInal Reading" class="form-control" v-model="final_reading" required>
            </div>
           
             <div class="col-md-6">
              <label class="form-label">Work Covered (KM)</label>
              <input type="number" class="form-control" placeholder="Enter Work Covered" v-model="work_covered" step="any" required >
            </div>

          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal" @click="resetForm()" id="close_editing">Close</button>
          <button type="submit" class="btn btn-primary" :disabled="loading">
            <span v-if="loading" class="spinner-border spinner-border-sm me-1" role="status" aria-hidden="true"></span>
            <span v-else>Update</span>
          </button>
        </div>
      </form>
    </div>
  </div>
</div>
<!-- Edit bincardTransactions modal ends -->

    </div>
</template>

<style>
    .table3 .actions svg {
        padding: 2px;
    }
</style>

<script setup>
    import { onMounted, ref, inject } from 'vue';
    import axios from 'axios';
    import { useMeta } from '@/composables/use-meta';
    import Multiselect from '@suadelabs/vue3-multiselect';
    import '@suadelabs/vue3-multiselect/dist/vue3-multiselect.css';
     import { useRoute } from 'vue-router';
    useMeta({ title: 'Asset Fuel Manager' });  
    const route = useRoute();
    const showMessage = inject('showMessage');
    const showError = inject('showError');
    const user_permissions = JSON.parse(localStorage.getItem("user")).permissions;
    const tableShimmer = inject('tableShimmer');JSON.parse(localStorage.getItem("user"))
    const loadingSpinner = inject('loadingSpinner'); 
    const register_bincard = ref(false);
    const printBinCard =inject('printBinCard')
    const columns = ref( ['Sno','date','action','quantity','task','initial_reading','final_reading','reading_difference','expected_ratio','work_target','work_covered','balance','actions']);    
    const bincardTransactions = ref([]);

    const table_option = ref({
        perPage: 20,
        perPageValues: [20, 50 ,100],
        skin: 'table table-hover table-striped',
        columnsClasses: { actions: 'actions text-center' },
        sortable: ['Sno','date', 'fuel_required','distance_cover','expected_ratio','expected_trips','trips_covered','fuel_balance',],        
        sortIcon: {
            base: 'sort-icon-none',
            up: 'sort-icon-asc',
            down: 'sort-icon-desc',
        },
        pagination: { nav: 'scroll', chunk: 5 },
        texts: {
            count: 'Showing {from} to {to} of {count}',
            filter: '',
            filterPlaceholder: 'Search...',
            limit: 'Results:',
        },
        resizableColumns: false,
    });

    const axiosInstance = axios.create({
        baseURL: process.env.VUE_APP_API_BASE_URL
    });


    const bincard = ref('')
    const getAssetBincard = async () => {
        loading.value = true;
        try {
            const response = await axiosInstance.get(`asset-bincards?asset_id=${route.params.asset_id}`);
            bincard.value = response.data
            bincardTransactions.value = response.data.transactions.map((item,index)=>({
         Sno: index + 1,
...item,
action : item.description,
expected_ratio : item.ratio,
reading_difference:
  (item.final_reading - item.initial_reading) >= 0
    ? (item.final_reading - item.initial_reading) + ' ' + response.data.asset.consuption_type
    : '---'
            }
            ))
        } catch (error) {
            showError(error.message);
        }finally{
            loading.value = false;
        }
    };

      const units = ref([])
      const unit = ref('')
      const getUnits = async () => {
        loading.value = true;
        const response = await axiosInstance.get('units')
        units.value = response.data
    };


    const loading = ref(false);
    const distance_cover = ref('')
    const description = ref('')
    const initial_reading = ref('')
    const ratio = ref('')
    const trips = ref('')
    const amount =ref('')
    const date = ref('')
    const work_target = ref('')
    const trips_covered = ref('')
    const quantity = ref('')
    const task = ref('')
const registerAssetBincardTransaction = async () => {
    loading.value = true;
    const payload = {
       asset_bincard_id: bincard.value.id,
       registrar_id: JSON.parse(localStorage.getItem("user")).id,
       direction: direction.value.id,
       unit: unit.value.short_name,
       description: `${direction.value.id === 'INCREMENT' ? 'Received' : 'Issued'}${description.value ? ' - ' + description.value : ''}`,
       task: task.value,
       quantity: quantity.value,
       date: date.value,
       ratio: ratio.value,
       work_target: work_target.value,
       initial_reading: initial_reading.value,
    };

    try {
        const response = await axiosInstance.post('asset-bincard-transactions', payload);
        getAssetBincard();
        showMessage('Record registered successfully!');
        resetForm();
    } catch (error) {
        console.log(error);
        showError(error.message);
    } finally {
        loading.value = false;
    }
};

const resetForm = () => {
     register_bincard.value = false;
    direction.value = ''
    task.value = ''
    quantity.value = ''
    date.value = ''
    ratio.value = ''
    work_target.value = ''
    description.value = ''
    initial_reading.value = ''
};


const editId = ref(null);
const final_reading = ref('')
const openEditModal = (row) => {
    work_covered.value = row.work_covered
    editId.value = row.id
    if(row.final_reading === null){
        final_reading.value = row.initial_reading
    }else{
     final_reading.value = row.final_reading
    }
 console.log(row.final_reading)
};

   const work_covered = ref('')
   const updateRecord = async () => {
    loading.value = true; 
    const payload = {
        work_covered : work_covered.value,
        final_reading : final_reading.value
    };

    try {
        const response = await axiosInstance.put(`asset-bincard-transactions/${editId.value}`, payload);
        getAssetBincard();
        showMessage('Record updated successfully!');
        document.getElementById('close_editing').click();
    } catch (error) {
      console.log(error);
        showError(error.message);
    } finally {
        loading.value = false;
    }
};

const printReport = async () => {
  try {
    const iframe = document.createElement("iframe");
    iframe.style.display = "none";
    document.body.appendChild(iframe);

    const printContent = `
<!DOCTYPE html>
<html>
<head>
<title>bincardTransactionss List</title>
<style>
  body { font-family: 'Courier New', monospace; margin: 40px; font-size: 11px; }
  .text-style { font-family: 'Courier New', monospace; }
  .company-title { text-align: center; }
  .title { text-align: center; font-size: 18px; margin: 10px 0; }
  table { width: 95%; border-collapse: collapse; margin: 10px auto; font-size: 11px; }
  th, td { border: 0.5px solid #000; padding: 4px; }
  th { background-color: #f8f8f8; font-weight: bold; }
  .text-right { text-align: right; }
  .text-center { text-align: center; }
  hr { border: 0; border-top: 0.5px solid #000; margin: 0px 0; }
  @media print { body { margin: 10mm; } }
</style>
</head>
<body>

<div class="company-title text-style">
  <i>
  <h1>COPEX CONTRACTORS CO.LTD</h1>
  <div><b>Address:</b> Msamvu, P.O.BOX 678, Morogoro</div>
  <div><b>Tel:</b> 0713 000 777, 0657 111 555</div>
  <div><b>Email:</b> info@copexcontractors.co.tz</div>
  <div><b>TIN:</b> 135-489-247 &nbsp;&nbsp; <b>VRN:</b> 40-032826C</div>
  </i>
</div>

<hr style="margin-top:20px">

<div class="title text-style">
  ${ asset.value.name } ${asset.value.type_name} FUEL MANAGEMENT
</div>

<hr>

<table>
  <thead>
    <tr>
      <th>No.</th>
      <th>Date</th>
      <th>Fuel Required</th>
      <th>Distance To Cover</th>
      <th>Expected Ratio</th>
      <th>Expected Trips</th>
      <th>Trips Covered</th>
      <th>Fuel Balance </th>
      <th>Initial Reading </th>
      <th>Final Reading </th>
    </tr>
  </thead>
  <tbody>
    ${bincardTransactions.value.map((v, index) => `
    <tr>
      <td>${index + 1}</td>
      <td>${v.date || '-'}</td>
      <td>${v.distance_cover || '-'}</td>
      <td>${v.fuel_required || '-'}</td>
      <td>${v.expected_ratio || '-'}</td>
      <td>${v.expected_trips || '-'}</td>
      <td>${v.trips_covered || '-'}</td>
      <td>${v.fuel_balance || '-'}</td>
      <td>${v.initial_reading || '-'}</td>
      <td>${v.final_reading || '-'}</td>
    </tr>
    `).join('')}
  </tbody>
</table>

</body>
</html>
`;

    const iframeDoc = iframe.contentWindow.document;
    iframeDoc.open();
    iframeDoc.write(printContent);
    iframeDoc.close();

    iframe.onload = () => {
      iframe.contentWindow.focus();
      iframe.contentWindow.print();
      setTimeout(() => {
        document.body.removeChild(iframe);
      }, 1000);
    };
  } catch (error) {
    showError(`Failed to print report: ${error.message}`);
  }
};

const direction = ref('')
const directions = ref([
    {
        id : 'INCREMENT',
        name : 'Received'
    },

      {
        id : 'DECREMENT',
        name : 'Issuing'
    },

])
const isRefreshing = ref(false)

    
    onMounted(() => {
        getAssetBincard();
        getUnits()
    });
</script>